                    <div>
                        <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>A traffic accident is a common problem in many countries because it causes social and economic issues. As the driver’s driving behavior is the primary cause of traffic crashes, the purpose of this study is to utilize the DTG data along with the dangerous driving behavior criteria for analyzing and understanding the driving behavior of each vehicle type. We begin the study by exploring the data to understand the characteristics of each vehicle type and then analyze the day trip of a vehicle to detect how many times the dangerous driving behavior occurred. We find that most of the dangerous driving behavior occurred in the morning and afternoon during the weekends which results from overspeed (59%), sharp turn (32%), and sudden u-turn (7%). Additionally, the common areas where dangerous driving behavior occurred as the top 3 cities are Seoul and its vicinity, Gwangju, and Jeonju. Regarding vehicle types, occasional cars, rural buses, and school buses showed the highest ratio of dangerous driving behavior, whereas taxis, rural buses, and rental buses showed the highest number of occurrences. We expect that others can utilize our results as references for future traffic accident prevention policy or traffic safety education for drivers.</p>
                        <p className="action"><a target="blank" href="http://www.cseric.or.kr/KeyDocs/tmp/0078-0092.pdf">[Paper]</a></p>
                    </div>
